*, *::before, *::after {
    box-sizing: border-box;
}

* {
    margin: 0;
    padding: 0;
    font: inherit;
}

html {
    scroll-behavior: smooth;
}

h1, h2, h3, p, blockquote, a, input, textarea, button, select, span {
    font-family: "Raleway";
    margin: 0;
}

body {
    margin: 0;
    height: 100%;
}